.Framsida{
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
    -ms-flex-pack: justify;
        justify-content: space-between;
    background-image: -webkit-gradient(linear, left top, left bottom, from(rgb(209, 107, 165, 1)), to(rgb(209, 107, 165, 0)));
    background-image: -webkit-linear-gradient(rgb(209, 107, 165, 1), rgb(209, 107, 165, 0));
    background-image: -o-linear-gradient(rgb(209, 107, 165, 1), rgb(209, 107, 165, 0));
    background-image: linear-gradient(rgb(209, 107, 165, 1), rgb(209, 107, 165, 0));
    height: 100%;   
}

.KunskapIkon{
    margin: 2rem 8rem;
}

.Framsida img {
    -ms-flex-item-align: center;
        align-self: center;
}



@media screen and ( max-width: 500px){

    .Framsida h1{
        margin: 4rem 0;
    }
    .zay {
        width: 300px;
    }   
    .Framsida{
        
        background-size: cover;
        top: 0;   
        left: 0;   
        bottom: 0;   
        right: 0;   
        position: absolute;   
        height: 70vh;
        width: 100%;
        margin: 0;
        padding: 0;
        
    }
    
}


