.Footer{
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: row;
        flex-direction: row;
    -ms-flex-pack: space-evenly;
        justify-content: space-evenly;
    -ms-flex-align: center;
        align-items: center;
    background-image: -webkit-gradient(linear, left top, left bottom, from(rgb(209, 107, 165, 0)), to(rgb(67, 99, 158)));
    background-image: -webkit-linear-gradient(rgb(209, 107, 165, 0), rgb(67, 99, 158));
    background-image: -o-linear-gradient(rgb(209, 107, 165, 0), rgb(67, 99, 158));
    background-image: linear-gradient(rgb(209, 107, 165, 0), rgb(67, 99, 158));
    
}

.Kontakt{
    color: black;    
}

.smallLinks{    
    padding: 1rem;
}

@media screen and ( max-width: 500px){

    .FootLänk{
        width: 8rem;
        display: -ms-flexbox;
        display: flex;
        -ms-flex-pack: distribute;
            justify-content: space-around;
    }
    .smallLinks{
        width: 60px;
        padding: 1rem;
    }
    .kon, .takt{
        font-size: 10px;

    }
   
}


